.modal-mask {
  position: fixed;
  z-index: 100;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  display: table;
  transition: opacity 0.3s ease;
}
.modal-wrapper {
  display: table-cell;
  vertical-align: middle;
}
.modal-container {
  width: 800px;
  height: 500px;
  margin: 0px auto;
  background-color: #fff;
  border-radius: 2px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.33);
  transition: all 0.3s ease;
  font-family: Helvetica, Arial, sans-serif;
  display: flex;
}
.modal-container .main {
  flex: 1;
  background: #2bb964 url(https://cdn.pixabay.com/photo/2014/05/02/21/50/laptop-336378_960_720.jpg) center center no-repeat;
  background-size: contain;
}
.modal-container .form {
  width: 270px;
  border-left: 1px solid #ccc;
}
.modal-container .form h3 {
  padding: 10px 20px;
  font-weight: normal;
  font-size: 16px;
  border-top: 1px solid #eee;
  cursor: pointer;
}
.modal-container .form h3:nth-of-type(2) {
  border-bottom: 1px solid #eee;
}
.modal-container .form .button {
  background-color: #2bb964;
  height: 36px;
  line-height: 36px;
  text-align: center;
  font-weight: bold;
  color: #fff;
  border-radius: 4px;
  margin-top: 18px;
  cursor: pointer;
}
.modal-container .form .login,
.modal-container .form .register {
  padding: 0px 20px;
  border-top: 1px solid #eee;
  height: 0;
  overflow: hidden;
  transition: height 0.5s;
}
.modal-container .form .login.show,
.modal-container .form .register.show {
  height: 193px;
}
.modal-container .form .login input,
.modal-container .form .register input {
  display: block;
  width: 100%;
  height: 35px;
  line-height: 35px;
  padding: 0 6px;
  border-radius: 4px;
  border: 1px solid #ccc;
  outline: none;
  font-size: 14px;
  margin-top: 10px;
}
.modal-container .form .login input:focus,
.modal-container .form .register input:focus {
  border: 3px solid #9dcaf8;
}
.modal-container .form .login p,
.modal-container .form .register p {
  font-size: 12px;
  margin-top: 10px;
  color: #444;
}
.modal-container .form .login .error,
.modal-container .form .register .error {
  color: red;
}
.modal-container .form .login {
  border-top: 0;
}
